      this.arr[i].s--;
      if (this.arr[i].s < 1) {
        this.arr[i].s = 1;
      }
    },
    shan(index){
      this.arr.splice(index,1)
    },
  },
  computed:{

    za(){
      let aa=0
      for(let i=0;i<this.arr.length;i++){
        aa+=this.arr[i].s*this.arr[i].myy
      }
      return aa
    }
