<script>
export default {
    data () {
        return {
            num:0
        }
    },
    created() {
        this.num = Number(this.$route.params.id)
    },
    computed:{
        newnarr(){
            let arr2 = []
            for (let i = 0; i < this.$store.state.newlist.length; i++) {
                if(this.$store.state.newlist[i].id != this.num){
                    arr2.push(this.$store.state.newlist[i])
                }
            }
            return arr2
        },
        newnarr2(){
            let arr4 = []
            for (let i = 0; i < this.$store.state.newnlist.length; i++) {
                if(this.$store.state.newnlist[i].id == this.num){
                    arr4.push(this.$store.state.newnlist[i])
                }
            }
            return arr4
        }
    },
    watch:{
        '$route.params.id'(){
            this.num = Number(this.$route.params.id)
        }
    }
}
</script>
