<template>
    <div class="tealists">
        <div class="tealist-hd">
            <!-- 头部 -->
            <h3 class="lf">茶叶</h3>
            <ul class="tea-list-hds">
                <li>全部</li>
                <li>绿茶</li>
                <li>黄茶</li>
                <li>乌龙茶</li>
                <li>红茶</li>
                <li>黑茶</li>
                <li>白茶</li>
                <li>花茶</li>
            </ul>
        </div>
        <ul class="tea-img clearfix">
            <li v-for="(i,$index) in setgo" :key="$index" class="animate__animated animate__fadeInUp animate__delay-.5s">
                <div class="tea-imgs">
                    <img :src="i.img6" alt="">
                </div>
                <div class="tea-cn">
                    <p class="u-title1">{{i.names1}}</p>
                    <p class="u-my">{{i.myy}}</p>
                    <div class="link">
                        <a href="" class="iconfont">&#xe639;</a>
                    </div>
                </div>
            </li>
        </ul>
        <div class="txt">
           <div @click="index<=0?0:index--">上一页</div>
           <div v-for='(i,$index) in page' :class='index == $index?"active":""' @click='index = $index' :key='$index'>
                {{i}}
           </div>
           <div @click='index>=page-1?page-1:index++'>下一页</div>
           <div @click='index=page-1'>最后一页</div>
        </div>
    </div>
</template>
<script>
export default {
      mounted(){
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
},
    data() {
        return {
            index:0,
            num:10
        }
    },
    computed:{
            page(){
                console.log(this.$store.state.tealist)
                let t = Math.ceil(this.$store.state.tealist.length/this.num);
                return t
            },
            setgo(){
                let p = this.$store.state.tealist.slice(this.index*this.num,this.index*this.num+this.num);
                return p;
            }
        }
}
</script>
