import Vue from 'vue'
import Router from 'vue-router'
import index from '../view/index.vue'
import no1 from '../view/no1'
import no2 from '../view/no2'
import no3 from '../view/no3'
import no4 from '../view/no4'
import no5 from '../view/no5'
import new1 from '../view/new1'
import newin from '../components/newin'
import teacho from '../view/teacho.vue'
import buycar from '../view/buycar.vue'

Vue.use(Router)
let routes = [
    { path: '/', redirect: '/index' },
    { path: '/index', name: 'index', component: index },
    { path: '/no1', name: 'no1', component: no1 },
    { path: '/no2', name: 'no2', component: no2 },
    { path: '/teacho/:id', name: 'teacho', component: teacho },
    { path: '/no3', name: 'no3', component: no3 },
    {path: '/no4', name: 'no4', component: no4, children: [
            { path: '/', redirect: 'newin' },
            { path: 'newin', name: 'newin', component: newin },
            { path: 'new1/:id', name: 'new1', component: new1 },
        ]
    },
    { path: '/no5', name: 'no5', component: no5 },
    { path: '/buycar', name: 'buycar', component: buycar },

]

export default new Router({
    // mode: 'history',
    routes,
    linkActiveClass: 'active'
})
