import bjimg from './components/bjimg.vue'
import lee1 from './components/lee1.vue'
import lee2 from './components/lee2.vue'
import lee3 from './components/lee3.vue'
import foot from './components/foot.vue'
import topimg from './components/topimg.vue'
import tall from './components/tall.vue'
import teac from './components/teac.vue'
import newin from './components/newin.vue'
import big from './components/big.vue'

let publics={
    install(Vue){
        Vue.component('bjimg',bjimg),
        Vue.component('lee1',lee1),
        Vue.component('lee2',lee2)
        Vue.component('lee3',lee3)
        Vue.component('foot',foot)
        Vue.component('topimg',topimg)
        Vue.component('tall',tall)
        Vue.component('teac',teac)
        Vue.component('newin',newin)
        Vue.component('big',big)
    }
}

export default publics